/* src/styles/home.css */

.container {
    max-width: 800px;
    margin: auto;
    text-align: center;
  }
  
  .heading {
    font-size: 24px;
    margin-bottom: 10px;
  }
  
  .paragraph {
    font-size: 16px;
    margin-bottom: 20px;
  }
  
  .subHeading {
    font-size: 20px;
    margin-bottom: 10px;
  }
  
  .list {
    list-style: none;
    padding: 0;
  }
  
  .listItem {
    margin-bottom: 10px;
  }
  
  .image {
    width: 100px;
    height: 50px;
    margin-right: 10px;
  }
  